import React, { Component } from 'react';
import { Switch, Route, Redirect} from 'react-router-dom';
import Header from './HeaderComponent';
//import FooterLarge from './FooterLarge';
import { HomePage } from './HomeComponents';
import About from './AboutComponent';
import ContactForm from './ContactComponent';
import { EventSpace } from './EventSpaceComponent';
import Menu from './MenuComponent';
import Press from './PressComponent';

//Route all pages
class Main extends Component {
    constructor(props) {
        super(props);

        this.state = {
          
        }
    }

    render() {
        return (
            <React.Fragment>
                <Header />
                <Switch>
                    <Route path={process.env.PUBLIC_URL + '/home'} render={() => <HomePage />} />
                    <Route path={process.env.PUBLIC_URL + '/about'} render={() => <About />} />
                    <Route path={process.env.PUBLIC_URL + '/contact'} render={() => <ContactForm />} />
                    <Route path={process.env.PUBLIC_URL + '/event_space'} render={() => <EventSpace />} />
                    <Route path={process.env.PUBLIC_URL + '/menu'} render={() => <Menu />} />
                    <Route path={process.env.PUBLIC_URL + '/press'} render={() => <Press />} />
                    <Redirect to={process.env.PUBLIC_URL + '/home'} />
                </Switch>
            </React.Fragment>
        );
    }
}

export default Main;